//
// Component: Main Sidebar
//

.main-sidebar {
  height: 100vh;
  overflow-y: hidden;
  z-index: $zindex-main-sidebar;
  background-color: #174A84;

  // Remove Firefox Focusring
  a {
    &:-moz-focusring {
      border: 0;
      outline: none;
    }
  }
}

.sidebar {
  height: calc(100% - (#{$main-header-height-inner} + #{$main-header-bottom-border-width}));
  overflow-y: auto;
  padding-bottom: $sidebar-padding-y;
  padding-left: 0px;
  padding-right: $sidebar-padding-x;
  padding-top: 40px;
  a.nav-link {
    i {
      color: white;
    }
  }

  .os-content {
    padding-left: 0px !important;
  }
}

// Sidebar user panel
.user-panel {
  position: relative;
  border-bottom: 1px solid rgba(251, 253, 255, 0.379);

  &,
  .info {
    overflow: hidden;
    white-space: nowrap;
  }

  .image {
    text-align: center;
    img {
      width: 190px;
    }
  }

  .info {
    display: inline-block;
    padding: 5px 5px 5px 30px;
    margin-top: 30px;

    h4,
    a {
      color: white;
      font-family: 'Lato', sans-serif;
    }

    h4 {
      margin-bottom: 0px;
      font-size: 16px;
      font-weight: bold;
    }

    a {
      font-size: 14px;
      font-weight: 400;
      text-decoration: none;
    }
  }

  .status,
  .dropdown-menu {
    font-size: $font-size-sm;
  }
}

// Sidebar navigation menu
.nav-sidebar {
  // All levels
  .nav-item {
    > .nav-link {
      font-family: 'Lato', sans-serif;
      font-size: 12px;
      border-radius: 0px;
      padding: 0;
      height: 50px;
      display: flex;
      align-items: center;
      padding-left: 10px;
      position: relative;
      .right {
        @include transition(transform $transition-fn $transition-speed);
      }
    }

    &:hover,
    &.active,
    &.menu-open {
      >.nav-link {
        color: #7FC520;
        
        i {
          color: #7FC520;
        }

        &::after {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          width: 5px;
          height: 100%;
          background-color: #7FC520;
        }
      }
    }
  }

  .nav-link > .right,
  .nav-link > p > .right {
    position: absolute;
    right: 1rem;
    top: .7rem;

    i,
    span {
      margin-left: .5rem;
    }

    &:nth-child(2) {
      right: 2.2rem;
    }
  }

  .menu-open {
    > .nav-treeview {
      display: block;
    }

    > .nav-link {
      color: #7FC520;
      i.right {
        @include rotate(90deg);
      }
    }
  }

  // First Level
  > .nav-item {
    margin-bottom: 0;
    border-radius: 0px;

    .nav-icon {
      margin-left: .05rem;
      font-size: 1.2rem;
      margin-right: .2rem;
      text-align: center;
      width: $sidebar-nav-icon-width;

      &.fa,
      &.fas,
      &.far,
      &.fab,
      &.glyphicon,
      &.ion {
        font-size: 1.1rem;
      }
    }

    .float-right {
      margin-top: 3px;
    }
  }

  // Tree view menu
  .nav-treeview {
    display: none;
    list-style: none;
    padding: 0;

    > .nav-item {
      > .nav-link {
        padding-left: 50px;
        font-size: 14px;
        > .nav-icon {
          width: $sidebar-nav-icon-width;
        }
      }
    }
  }

  &.nav-child-indent {
    .nav-treeview {
      transition: padding $transition-speed $transition-fn;
      padding-left: 1rem;

      .text-sm & {
        padding-left: .5rem;
      }
    }

    &.nav-legacy {
      .nav-treeview {      
        .nav-treeview {
          padding-left: 2rem;
          margin-left: -1rem;

          .text-sm & {          
            padding-left: 1rem;
            margin-left: -.5rem;
          }
        }
      }
    }
  }

  .nav-header {
    font-size: .9rem;
    padding: $nav-link-padding-y;

    &:not(:first-of-type) {
      padding: 1.7rem 1rem .5rem;
    }
  }

  .nav-link p {
    display: inline-block;
    animation-name: fadeIn;
    animation-duration: $transition-speed;
    animation-fill-mode: both;
    margin: 0;
  }
}

#sidebar-overlay {
  @include media-breakpoint-down(md) {
    .sidebar-open & {
      display: block;
    }
  }

  background-color: rgba($black, 0.1);
  bottom: 0;
  display: none;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: $zindex-main-sidebar - 1;
}

.sidebar-mini .main-sidebar:not(.sidebar-no-expand),
.sidebar-mini-md .main-sidebar:not(.sidebar-no-expand),
.sidebar-mini .main-sidebar:not(.sidebar-no-expand):hover,
.sidebar-mini-md .main-sidebar:not(.sidebar-no-expand):hover,
.sidebar-mini .main-sidebar.sidebar-focused,
.sidebar-mini-md .main-sidebar.sidebar-focused {
  .nav-compact.nav-sidebar.nav-child-indent:not(.nav-flat) .nav-treeview {
    padding-left: 1rem;
    margin-left: -.5rem;
  }
}

// Nav Flat
.nav-flat {
  margin: (-$sidebar-padding-x/2) (-$sidebar-padding-x) 0;

  .nav-item {
    > .nav-link {
      border-radius: 0;
      margin-bottom: 0;

      > .nav-icon {
        margin-left: .55rem;
      }
    }
  }

  &:not(.nav-child-indent) {
    .nav-treeview {  
      .nav-item {
        > .nav-link {
          > .nav-icon {
            margin-left: .4rem;
          }
        }
      }
    }
  }

  &.nav-child-indent {
    .nav-treeview {
      padding-left: 0;

      .nav-icon {
        margin-left: .85rem;
      }

      .nav-treeview {
        border-left: .2rem solid;

        .nav-icon {
          margin-left: 1.15rem;
        }

        .nav-treeview {
          .nav-icon {
            margin-left: 1.45rem;
          }

          .nav-treeview {
            .nav-icon {
              margin-left: 1.75rem;
            }

            .nav-treeview {
              .nav-icon {
                margin-left: 2.05rem;
              }
            }
          }
        }
      }
    }
  }

  .sidebar-collapse &.nav-child-indent {

    .nav-treeview {
      .nav-icon {
        margin-left: .55rem;
      }

      .nav-link {
        padding-left: calc(#{$nav-link-padding-x} - .2rem);
      }

      .nav-treeview {
        .nav-icon {
          margin-left: .35rem;
        }

        .nav-treeview {
          .nav-icon {
            margin-left: .15rem;
          }

          .nav-treeview {
            .nav-icon {
              margin-left: -.15rem;
            }

            .nav-treeview {
              .nav-icon {
                margin-left: -.35rem;
              }
            }
          }
        }
      }
    }
  }

  .sidebar-mini .main-sidebar:not(.sidebar-no-expand):hover &,
  .sidebar-mini-md .main-sidebar:not(.sidebar-no-expand):hover &,
  .sidebar-mini .main-sidebar.sidebar-focused &,
  .sidebar-mini-md .main-sidebar.sidebar-focused & {
    &.nav-compact.nav-sidebar .nav-treeview {
      .nav-icon {
        margin-left: .4rem;
      }
    }

    &.nav-sidebar.nav-child-indent .nav-treeview {
      .nav-icon {
        margin-left: .85rem;
      }

      .nav-treeview {
        .nav-icon {
          margin-left: 1.15rem;
        }

        .nav-treeview {
          .nav-icon {
            margin-left: 1.45rem;
          }

          .nav-treeview {
            .nav-icon {
              margin-left: 1.75rem;
            }

            .nav-treeview {
              .nav-icon {
                margin-left: 2.05rem;
              }
            }
          }
        }
      }
    }
  }

  .nav-icon {
    @include transition(margin-left $transition-fn $transition-speed);
  }

  .nav-treeview {
    .nav-icon {
      margin-left: -.2rem;
    }
  }

  &.nav-sidebar > .nav-item {
    .nav-treeview,
    > .nav-treeview {
      background: rgba($white, .05);

      .nav-item {
        > .nav-link {
          border-left: .2rem solid;
        }
      }
    }
  }
}

.nav-legacy {
  margin: (-$sidebar-padding-x/2) (-$sidebar-padding-x) 0;

  &.nav-sidebar .nav-item {
    > .nav-link {
      border-radius: 0;
      margin-bottom: 0;

      > .nav-icon {
        margin-left: .55rem;

        .text-sm & {
          margin-left: .75rem;
        }
      }
    }
  }

  &.nav-sidebar > .nav-item {
    > .nav-link {
      &.active {
        background: inherit;
        border-left: 3px solid transparent;
        box-shadow: none;

        > .nav-icon {
          margin-left: calc(.55rem - 3px);
          
          .text-sm & {
            margin-left: calc(.75rem - 3px);
          }
        }
      }
    }
  }

  .text-sm &.nav-sidebar.nav-flat .nav-treeview {
    .nav-item {
      > .nav-link {
        > .nav-icon {
          margin-left: calc(.75rem - 3px);
        }
      }
    }
  }

  .sidebar-mini &,
  .sidebar-mini-md & {
    > .nav-item .nav-link {
      .nav-icon {
        @include transition(margin-left $transition-fn $transition-speed);
        margin-left: .75rem;
      }
    }
  }

  .sidebar-mini.sidebar-collapse .main-sidebar.sidebar-focused &.nav-child-indent,
  .sidebar-mini.sidebar-collapse .main-sidebar:hover &.nav-child-indent,
  .sidebar-mini-md.sidebar-collapse .main-sidebar.sidebar-focused &.nav-child-indent,
  .sidebar-mini-md.sidebar-collapse .main-sidebar:hover &.nav-child-indent {
    .nav-treeview {
      padding-left: 1rem;

      .nav-treeview {
        padding-left: 2rem;
        margin-left: -1rem;
      }
    }
  }

  .sidebar-mini.sidebar-collapse.text-sm .main-sidebar.sidebar-focused &.nav-child-indent,
  .sidebar-mini.sidebar-collapse.text-sm .main-sidebar:hover &.nav-child-indent,
  .sidebar-mini-md.sidebar-collapse.text-sm .main-sidebar.sidebar-focused &.nav-child-indent,
  .sidebar-mini-md.sidebar-collapse.text-sm .main-sidebar:hover &.nav-child-indent {
    .nav-treeview {
      padding-left: .5rem;

      .nav-treeview {
        padding-left: 1rem;
        margin-left: -.5rem;
      }
    }
  }

  .sidebar-mini.sidebar-collapse &,
  .sidebar-mini-md.sidebar-collapse & {
    > .nav-item > .nav-link {
      .nav-icon {
        margin-left: .55rem;
      }

      &.active {
        > .nav-icon{
          margin-left: .36rem;
        }
      }
    }

    &.nav-child-indent {   
      .nav-treeview {
        .nav-treeview {
          padding-left: 0;
          margin-left: 0;
        }
      }
      }
    }


  .sidebar-mini.sidebar-collapse.text-sm &,
  .sidebar-mini-md.sidebar-collapse.text-sm & {
    > .nav-item > .nav-link {
      .nav-icon {
        margin-left: .75rem;
      }

      &.active {
        > .nav-icon{
          margin-left: calc(.75rem - 3px);
        }
      }
    }
  }

  [class*='sidebar-dark'] & {
    &.nav-sidebar > .nav-item {
      .nav-treeview,
      > .nav-treeview {
        background: rgba($white, .05);
      }

      > .nav-link.active {
        color: $sidebar-dark-active-color;
      }
    }

    .nav-treeview > .nav-item > .nav-link {
      &.active,
      &:focus,
      &:hover {
        background: none;
        color: $sidebar-dark-active-color;
      }
    }
  }

  [class*='sidebar-light'] & {
    &.nav-sidebar > .nav-item {
      .nav-treeview,
      > .nav-treeview {
        background: rgba($black, .05);
      }

      > .nav-link.active {
        color: $sidebar-light-active-color;
      }
    }

    .nav-treeview > .nav-item > .nav-link {
      &.active,
      &:focus,
      &:hover {
        background: none;
        color: $sidebar-light-active-color;
      }
    }
  }
}

.nav-collapse-hide-child {
  .menu-open > .nav-treeview {
    max-height: min-content;
    animation-name: fadeIn;
    animation-duration: $transition-speed;
    animation-fill-mode: both;
  }

  .sidebar-collapse & {
    .menu-open > .nav-treeview {
      max-height: 0;
      animation-name: fadeOut;
      animation-duration: $transition-speed;
      animation-fill-mode: both;
    }
  }

  .sidebar-mini.sidebar-collapse .main-sidebar.sidebar-focused &,
  .sidebar-mini.sidebar-collapse .main-sidebar:hover &,
  .sidebar-mini-md.sidebar-collapse .main-sidebar.sidebar-focused &,
  .sidebar-mini-md.sidebar-collapse .main-sidebar:hover & {
    .menu-open > .nav-treeview {
      max-height: min-content;
      animation-name: fadeIn;
      animation-duration: $transition-speed;
      animation-fill-mode: both;
    }
  }
}

// Nav Compact
.nav-compact {
  .nav-link,
  .nav-header {
    padding-top: ($nav-link-padding-y / 2);
    padding-bottom: ($nav-link-padding-y / 2);
  }

  .nav-header:not(:first-of-type) {
    padding-top: ($nav-link-padding-y * 1.5);
    padding-bottom: ($nav-link-padding-y / 2);
  }

  .nav-link > .right,
  .nav-link > p > .right {
    top: .465rem;
  }

  .text-sm & {
    .nav-link > .right,
    .nav-link > p > .right {
      top: .7rem;
    }
  }
}

// Sidebar inline input-group fix
.sidebar .form-inline .input-group {
  width: 100%;
}

.sidebar-collapse {
  .user-panel {
    display: none !important;
  }
}